import React from 'react';
import * as ReactDOM from 'react-dom/client';
import reactToWebComponent from 'react-to-webcomponent';
import { Config, configType } from './types/Config';
import LiveChat from './components/livechat/LiveChat';

class WebComponentWrapper extends React.Component<{ configs: string }> {
  static propTypes: {
    configs: String;
  };

  render = () => {
    // Use a default value for configs if it is not provided or invalid
    let configs:Config[] = [];
    try {
      console.log(this.props)
      if (this.props.configs) {
        
        const parsedConfigs = JSON.parse(this.props.configs);
        // Check if parsedConfigs is an array of valid checklist items
        if (Array.isArray(parsedConfigs) && parsedConfigs.every(configType)) {
          configs = parsedConfigs;
        }
      }else{
        console.error("no props given#333")
      }
    } catch(e) {
      // Ignore any parsing errors and use the default value
      console.error(e)
    }

    return <LiveChat configs={configs} />;
  }
}

WebComponentWrapper.propTypes = {
  configs: new String,
}
const WrappedLiveChat = reactToWebComponent(
  WebComponentWrapper,
  React,
  ReactDOM,
);

customElements.define("flipper-livechat", WrappedLiveChat);
